import React from 'react';
import { courier, COURIER } from 'delivery-tracker';
import { useEffect } from 'react';



export default function OrderTracker(){





useEffect(() => {
    const getTrace = async () => {
      try {
        const koreaPost = courier(COURIER.UPS.CODE);
        const traceResult = await koreaPost.trace('1Z12345E6605272234', function (err, result) {
            if (err) {
              console.log(`err: `, err)
            }
            console.log(result)
            
        });
        console.log(traceResult);
      } catch (error) {
        console.log('Error getting trace:', error);
      }
    };
    getTrace();

},[])

return <h1 style={{marginTop:"80px"}}>Track Order Page</h1>

}