import data from '../../../data/products.json'

import React from "react";
import { useParams } from 'react-router-dom';

import ProductInfoBuy from './ProductInfoBuy';
import { useState } from 'react';
import { RatingStar } from 'rating-star';
import './ProductPage.css'
import boxItem from '../../../images/boxItem.png'


export default function ProductPage(props){

    const { productId } = useParams();
    const product= data.find(p=>{return p.id==productId})



    const [selectedStyle, setSelectedStyle] = useState('Black Kitten');
  
    const handleStyleChange = (style) => {
      setSelectedStyle(style);
    };





    return <>{product
        ?
        <>
        
        <div style={{display:'flex', justifyContent:"center",  marginTop:"25vh"}}> 
        <div>
           <ProductInfoBuy product={product} onAddToCart={props.onAddToCart}/>
           
            </div>



            <div style={{marginLeft:"10px"}}>
      <h1 className="product-title">{product.name}</h1>
      <p className="product-rating">  <RatingStar

        maxScore={50}
        id="123"
        rating={46}
      />(14)</p>
      <p className="product-price">1,160.14 RSD</p>
      <p className="product-style-label">Style - {selectedStyle}</p>
      <div className="product-style-options">
     
        <button className="product-style-button" onClick={() => handleStyleChange('Black Kitten')}>
          <img src={boxItem} alt="Black Kitten" className="product-style-image" />
        </button>
        <button className="product-style-button" onClick={() => handleStyleChange('Gray Kitten')}>
          <img src={boxItem} alt="Gray Kitten" className="product-style-image" />
        </button>
        <button className="product-style-button" onClick={() => handleStyleChange('White Kitten')}>
          <img src={boxItem} alt="White Kitten" className="product-style-image" />
        </button>
      </div>
      <button className="add-to-cart-button">Add to Cart</button>
    </div>






        </div>
        </>
    :
    <p style={{marginTop:"100px"}}>Page not found.</p>}</>
}