import React, { useState } from 'react';
import { AppBar, Toolbar, IconButton, Badge, MenuItem, Menu, Typography } from '@material-ui/core';
import { ShoppingCart } from '@material-ui/icons';
import { Link, useLocation } from 'react-router-dom';




import logo from '../../assets/commerce.png';
import useStyles from './styles';

const PrimarySearchAppBar = ({ totalItems }) => {
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);
  const classes = useStyles();
  const location = useLocation();

  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleMobileMenuClose = () => setMobileMoreAnchorEl(null);

  const mobileMenuId = 'primary-search-account-menu-mobile';

  const renderMobileMenu = (
    <Menu anchorEl={mobileMoreAnchorEl} anchorOrigin={{ vertical: 'top', horizontal: 'right' }} id={mobileMenuId} keepMounted transformOrigin={{ 
      vertical: 'top', horizontal: 'right' }} open={isMobileMenuOpen} onClose={handleMobileMenuClose}>
      <MenuItem>
        <IconButton component={Link} to="/cart" aria-label="Show cart items" color="inherit">
          <Badge badgeContent={totalItems} color="secondary">
            <ShoppingCart />
          </Badge>
        </IconButton>
        <p>Cart</p>
      </MenuItem>
    </Menu>
  );
  console.log(location.pathname)

  return (
    <>
      <AppBar  className={classes.appBar} color="inherit">
        <Toolbar className={classes.toolbar}>
          <Typography component={Link} to="/" variant="h6" className={classes.title} color="inherit">
            <img src={logo} alt="Game Smoke Gear" height="25px" className={classes.image} />GameSmoke Gear</Typography>
          <div className={classes.grow} >
            <Link to='/' className={`${classes.linkStyle} ${location.pathname === '/' ? classes.currentLink : ''}`}>Home</Link>
            <Link to='/shop' className={`${classes.linkStyle} ${location.pathname === '/shop' ? classes.currentLink : ''}`}>Shop</Link>
            <Link to='/about-us' className={`${classes.linkStyle} ${location.pathname === '/about-us' ? classes.currentLink : ''}`}>About</Link>
            <Link to='/contact-us' className={`${classes.linkStyle} ${location.pathname === '/contact-us' ? classes.currentLink : ''}`}>Contact us</Link>
            
          </div>
          
          <div className={classes.button}>
            <IconButton component={Link} to="/cart" className={classes.cartStyle}   aria-label="Show cart items" color="inherit">
              <Badge badgeContent={totalItems} color="secondary">
                <ShoppingCart />
              </Badge>
            </IconButton>
          </div>
          
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      
    </>
  );
};

export default PrimarySearchAppBar;
