import React from 'react';
import './policyCard.css'


export function PolicyMiniCard(props){
 
  return <div  className={props.bigTopMargin?'policy-main-div big-top-margin':'policy-main-div'}>
  {props.children}
  
  </div>
}

export default function PolicyCard(props){

    return <div className={props.smallContent?'policy-background-div bigBackDiv':'policy-background-div'}
  > <PolicyMiniCard >{props.children}</PolicyMiniCard>
  
    </div>


}
